.map{
  width: 100%;
  height: 100%;
  background-image: url("../../img/bg.png");
  background-size: 100% 100%;
  .bar{
    display: flex;
    padding-top: 12.94px;
    align-items: center;
    font-size:18px;
    font-family:Source Han Sans SC;
    .back{
      width: 29.25px;
      height: 29.25px;
      margin-left: 14.06px;
      cursor: pointer;
    }
    .title{
      margin-left: 30px;
      color: rgba(8,127,237,1);
    }
    .status{
      width: 95px;
      height: 49px;
      line-height: 49px;
      text-align: center;
      background-image: url("../../img/infoBG.png");
      background-size: 100% 100%;
    }
    .forRepair{
      color: #FF3A2B;
    }
  }
  .container{
    height: 80%;
    width: 100%;
    background-image: url("../../img/map-bg.png");
    background-clip: border-box;
    background-size: 100% 100% !important;
    padding: 20px 40px 45px 30px;
    overflow: hidden;
    #myMap{
      height: 100%;
      box-sizing: border-box;
      margin-top: 10px;
      width: 100%;
      overflow: hidden;
    }
  }
}
