.device{
  width: 1570px;
  padding-top: 20px;
  padding-left: 8px;
  background-image: url('../../img/bg.png');
  height: 100%;
  display: flex;
  .left{
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    #model{
      width: 100%;
      height: 90%;
      position: relative;
      background-image: url("../../img/device-bg.png");
      background-repeat: no-repeat;
      background-position: bottom;
    }
    .repair-btn{
      position: absolute;
      bottom: 20%;
      width:340px;
      height:100px;
      background-image: url('../../img/repair.png');
      background-size: 100% 100%;
      border-radius:10px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      .icon{
        width: 40px;
        height: 40px;
      }
      .text{
        margin-left: 19px;
        font-size:18px;
        font-family:Source Han Sans SC;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
    }
    .repair-btn:hover{
      background-image: url("../../img/repair-act.png");
      background-size: 100% 100%;
    }
  }
  .right{
    margin-left: 38px;
    .position{
      width: 100%;
      height: 93px;
      padding-left: 25px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-position: bottom center;
      margin-bottom: 5px;
      .icon{
        width: 19px;
        height: 29px;
      }
      .title{
        font-size:12px;
        font-family:Source Han Sans SC;
        font-weight:400;
        color:rgba(111,199,255,1);
        margin-left: 17px;
      }
      .desc{
        margin-left: 35px;
        font-size:24px;
        font-family:Source Han Sans SC;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
    }
  }
}
